@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

.container {
  max-width: 140rem;
  margin: 0 auto;
  padding: 0 3rem;
  margin-top: 10rem;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

/*  */
.heading-secondary {
  font-size: 32px;
  color: #343;
  margin-bottom: 4rem;
}
button {
  font-family: "Roboto", sans-serif;
}

/* common  */

/* Re useable class */
/* height ,width */
.w-full {
  width: 100%;
}
.w-70 {
  width: 70%;
}
.w-fit {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.h-524 {
  height: 524px;
}
.h-screen {
  height: 100vh;
}
/* font */
.font-semibold {
  font-weight: 500;
}
.text-2 {
  font-size: 2rem;
}
/* flex box */
.flex {
  display: flex;
}
.items-center {
  align-items: center;
}
.items-start {
  align-items: start;
}
.items-end {
  align-items: end;
}
.justify-start {
  justify-content: start;
}
.justify-center {
  justify-content: center;
}
.justify-between {
  justify-content: space-between;
}
.justify-end {
  justify-content: end;
}
.flex-1 {
  flex: 1 1;
}
.gap-2 {
  grid-gap: 2rem;
  gap: 2rem;
}
.gap-4 {
  grid-gap: 2rem;
  gap: 2rem;
}
.border-1 {
  border: 1px solid black;
}
.radius-5 {
  border-radius: 5px;
}
.rounded-full {
  border-radius: 100%;
}
.shadow-base {
  box-shadow: 2px -1px 15px 1px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 2px -1px 15px 1px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 2px -1px 15px 1px rgba(0, 0, 0, 0.2);
}
.cursor-pointer {
  cursor: pointer;
}
.not-allowed {
  cursor: not-allowed !important;
  opacity: 0.5;
}
.border-right {
  border-right: 1px solid gray !important;
}
.list-none {
  list-style: none;
}
.ml-1 {
  margin-left: 1rem;
}
.mb-1 {
  margin-bottom: 1rem;
}
.mb-2 {
  margin-bottom: 2rem;
}
.mb-5 {
  margin-bottom: 5rem;
}
.mt-2 {
  margin-top: 2rem;
}
.mr-1 {
  margin-right: 1rem;
}
.p-2 {
  padding: 2rem;
}
.pb-2 {
  padding-bottom: 2rem;
}
.pr-2 {
  padding-right: 2rem;
}
.transformX-1 {
  -webkit-transform: translateX(1rem);
          transform: translateX(1rem);
}
.text-center {
  text-align: center;
}
/* positioning */
.fixed {
  position: fixed;
}
.relative {
  position: relative;
}
.absolute {
  position: absolute;
}
.top-0 {
  top: 0;
}
.top-3 {
  top: 3%;
}
.top-50 {
  top: 50%;
}
.left-0 {
  left: 0;
}
.left-50 {
  left: 50%;
}
.right-3 {
  right: 3%;
}
.bottom-4 {
  bottom: 4%;
}

/* z-indexing */
.z-1 {
  z-index: 1;
}
.z-2 {
  z-index: 2;
}

/* Navbar.css */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  padding: 2rem 3rem;
}

.nav-item {
  color: white;
  text-decoration: none;
  margin: 0 0.5rem;
  font-weight: bold;
}

.nav-item:hover {
  color: #ff9900;
}

/* backdrop */
.backdrop-bg {
  background-color: rgba(0, 0, 0, 0.6);
}

/* Form */

.create-user-modal {
  background-color: white;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 3rem 4rem;
  border-radius: 10px;
}
.form {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  width: 350px;
}
.form-label {
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
}

.form-input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: border-color 0.3s ease;
}

.form-input:focus {
  border-color: #3498db;
  outline: none;
}
.error {
  font-size: 14px;
  color: red;
  margin-top: 2px;
}
.submit-button {
  background-color: #3498db;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #2581c7;
}

/* Pagination */
.pagination-btn-box button {
  border: none;
  background: transparent;
  color: #2581c7;
  cursor: pointer;
  font-size: 1.5rem;
}

/* Loading spinner */

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db; /* Change to your preferred color */
  border-radius: 50%;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* tab */
.tab-buttons {
  border-bottom: 1px solid gray;
}
.tab-content {
  padding: 25px;
}

.add-user-button {
  background-color: #3498db;
  color: #ffffff;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s, color 0.2s;
}

.add-user-button:focus {
  background-color: #79b9e4;
  outline: none;
}
/* user grid */
.user-grid {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  padding: 2rem;
}

.grid-header,
.grid-row {
  display: flex;
  padding: 1rem;
  border-bottom: 1px solid #ccc;
}

.grid-header {
  font-weight: bold;
  font-weight: bold;
  background-color: #f0f0f0;
  color: #333;
}

.grid-row:last-child {
  border-bottom: none;
}

.btn {
  background: transparent;
  border: none;
  padding: 20px;
  font-size: 16px;
  width: 100%;
}
.btn-active {
  border-bottom: 1px solid blue;
  color: blue;
}


